<template>
  <div>
    <div class="playersListsWrap">
      <div>
        <h3 v-if="!registeredUsers.length">  </h3>
        <h3 v-if="registeredUsers.length"> :</h3>
        <ol>
          <li v-for="user in registeredUsers" class="userListItem">
            <span class="userName">{{user.name}}</span>
            <button @click.stop="unregisterUser(user.id)">x</button>
          </li>
        </ol>
        <span> <strong>{{placesLeft}}</strong></span>
      </div>

      <div v-if="registeredUsers.length !== seeds">
        <h3> :</h3>
        <ol>
          <li v-for="user in pickedUsers" class="userListItem">
            <span>{{user.name}}</span>
            <button @click.stop="removeUser(user)">x</button>
          </li>
        </ol>

        <suggest
          v-if="!isFull"
          :items="suggestions"
          @input="findUser"
          placeholder="   "
          @pick="pickUser"
        />
        <button @click="saveChanges">    </button>
      </div>
    </div>
    <button @click="finishRegistration"> !</button>
  </div>
</template>

<script>
  import Suggest from './Suggest.vue';

  export default {
    name: 'new-stage',
    components: {
      Suggest
    },
    props: [
      'suggest-user',
      'submit-users',
      'seeds',
      'registered-users',
      'unregister-user',
      'close-registration'
    ],
    data: function () {
      return {
        suggestions: [],
        inputValue: '',
        pickedUsers: []
      };
    },
    computed: {
      placesLeft: function () {
        return this.seeds - this.registeredUsers.length - this.pickedUsers.length;
      },
      isFull: function () {
        return !this.placesLeft;
      }
    },
    methods: {
      findUser(e) {
        this.inputValue = e.target.value.trim();

        if (!this.inputValue) {
          return;
        }

        this.suggestUser(e.target.value)
          .then(d => {
            this.suggestions = Object.values(d).map(i => ({...i, display: i.name}));
          });
      },
      pickUser(user) {
        this.suggestions = [];
        if (
          !this.pickedUsers.find(pickedUser => user.id === pickedUser.id) &&
          !this.registeredUsers.find(pickedUser => user.id === pickedUser.id)
        ) {
          this.pickedUsers = this.pickedUsers.concat(user);
        }
      },
      removeUser(user) {
        this.pickedUsers = this.pickedUsers.filter(u => u.id !== user.id);
      },
      saveChanges() {
        this.submitUsers(this.pickedUsers)
          .then(users => {
            if (Array.isArray(users)) {
              this.pickedUsers = [];
            }
          });
      },
      finishRegistration() {
        if (confirm(' ?   ,     !')) {
          this.closeRegistration();
        }
      }
    }
  }
</script>

<style scoped>
  .playersListsWrap {
    display: flex;
    gap: 30px;
  }

  .userListItem {
    display: flex;
    justify-content: space-between;
  }

  .userName {
    margin-right: 20px;
  }
</style>



