<template>
  <div>
    <div class="header-controls">
      <div>
        <label>
          <h4>: <button @click="toggleNewTournamentPopup"> + </button></h4>
          <select @change="handleTournamentSelection" :value="selectedTournament">
            <option :value="null"> </option>
            <option v-for="tournament in tournaments" :value="tournament.id">{{tournament.title}}</option>
          </select>
        </label>
        <div v-if="!selectedTournament">
          <button class="pseudo-link" v-for="ong in ongoingTournaments" @click="selectTournament(ong.id)">{{ong.title}}</button>
        </div>
      </div>

      <div>
        <label v-if="selectedTournament">
          <h4>: <button @click="toggleNewTournamentStagePopup"> + </button></h4>
          <select @change="handleStageSelection" :value="selectedStage">
            <option :value="null"> </option>
            <option v-for="stage in tournaments[selectedTournament].stages" :value="stage.id">{{stage.title}}</option>
          </select>
        </label>

        <div v-if="selectedTournament && selectedStage">
          <button
            class="pseudo-link"
            @click="selectPreviousStage"
            :disabled="stagePosition < 1"
          >
             
          </button>
          <button
            class="pseudo-link"
            @click="selectNextStage"
            :disabled="stagePosition >= tournaments[selectedTournament].stages.length - 1"
          >
             
          </button>
        </div>
      </div>
    </div>

    <modal :visible="creatingNewTournament" @close="toggleNewTournamentPopup">
      <div class="modal-content">
        <h5>:</h5>
        <input v-model="newTournamentData.title" />
        <h5>
          :
        </h5>
        <textarea v-model="newTournamentData.description"></textarea>
        <h5>
          :
        </h5>
        <textarea v-model="newTournamentData.reglament"></textarea>
        <h5>
           :
        </h5>
        <select v-model="newTournamentData.net_size">
          <option>16</option>
          <option>32</option>
          <option>64</option>
          <option>128</option>
          <option>256</option>
        </select>
        <h5>
           :
        </h5>
        <select v-model="newTournamentData.net_type">
          <option>2-0</option>
          <option>2-1</option>
        </select>
        <br />
        <label>
          <input type="checkbox" v-model="newTournamentData.is_rating" /> 
        </label>
        <br />
        <datepicker :value="newTournamentData.start_date" @change="changeNewTournamentStart" />
        <datepicker :value="newTournamentData.end_date" @change="changeNewTournamentEnd" />
        <div>
          <button @click="toggleNewTournamentPopup"></button>
          <button @click="submitNewTournament"></button>
        </div>
      </div>
    </modal>

    <modal :visible="creatingNewTournamentStage" @close="toggleNewTournamentStagePopup" v-if="selectedTournament">
      <div class="modal-content">
        <h5>:</h5>
        <input v-model="newTournamentStageData.title" />
        <h5>
           :
        </h5>
        <select v-model="newTournamentStageData.net_size">
          <option>16</option>
          <option>32</option>
          <option>64</option>
          <option>128</option>
          <option>256</option>
        </select>
        <h5>
           :
        </h5>
        <select v-model="newTournamentStageData.net_type">
          <option>2-0</option>
          <option>2-1</option>
        </select>
        <br />
        <datepicker :value="newTournamentStageData.start_date" @change="changeNewTournamentStageStart" />
        <datepicker :value="newTournamentStageData.end_date" @change="changeNewTournamentStageEnd" />
        <div>
          <button @click="toggleNewTournamentStagePopup"></button>
          <button @click="submitNewTournamentStage"></button>
        </div>
      </div>
    </modal>
  </div>
</template>

<script>
  import Modal from './Modal.vue';
  import Datepicker from './Datepicker.vue';

  export default {
    name: "tournament-table",
    components: {
      Modal,
      Datepicker
    },
    props: [
      'get-tournaments',
      'get-tournament-stages',
      'pick-stage',
      'save-tournament',
      'save-tournament-stage'
    ],
    created: function() {
      this.obtainTournaments()
        .then(() => {
          this.fillNewTournamentData();
          this.fillNewTournamentStageData();
        });
    },
    data: function () {
      return {
        tournaments: {},
        showOutdated: false,
        selectedTournament: null,
        selectedStage: null,
        ongoingTournamentsIds: [],
        ongoingTournaments: [],
        creatingNewTournament: false,
        creatingNewTournamentStage: false,
        newTournamentData: {},
        newTournamentStageData: {}
      };
    },
    computed: {
      stagePosition: function () {
        return this.tournaments[this.selectedTournament].stages.findIndex(stage => stage.id === this.selectedStage);
      }
    },
    methods: {
      obtainTournaments() {
        return this.getTournaments && this.getTournaments()
          .then(tournaments => {
            this.tournaments = tournaments;
            this.ongoingTournamentsIds = Object.keys(this.tournaments)
              .filter(id => {
                return Date.parse(tournaments[id].end_date) >= Date.now();
              });
            this.remapOngoing();
          })
      },
      fillNewTournamentData() {
        this.newTournamentData = {
          title: '',
          description: '',
          reglament: '',
          net_size: 16,
          net_type: '2-1',
          is_rating: false,
          start_date: Date.now(),
          end_date: Date.now() + 1000 * 60 * 60 * 24 * 7
        };
      },
      fillNewTournamentStageData() {
        const defaultValues = this.tournaments[this.selectedTournament];

        this.newTournamentStageData = {
          title: '',
          net_size: (defaultValues && defaultValues.net_size) || 16,
          net_type: (defaultValues && defaultValues.net_type) || '2-1',
          start_date: Date.now(),
          end_date: Date.now() + 1000 * 60 * 60 * 24 * 7
        };
      },
      remapOngoing() {
        this.ongoingTournaments = this.ongoingTournamentsIds
          .map(id => this.tournaments[id]);
      },
      handleTournamentSelection(e) {
        this.selectTournament(e.target.value);
      },
      selectTournament(id = null) {
        if (this.selectedTournament !== id) {
          this.selectedStage = null;
        }

        this.selectedTournament = id;

        if (this.tournaments[this.selectedTournament] &&
          Array.isArray(this.tournaments[this.selectedTournament].stages) &&
          this.tournaments[this.selectedTournament].stages.length > 0) {
          return;
        }

        this.getTournamentStages && this.getTournamentStages(id)
          .then(tournament => {
            this.tournaments = {
              ...this.tournaments,
              [id]: tournament
            };
            this.remapOngoing();

            if (tournament.stages.length) {
              this.selectStage(tournament.stages[0].id);
            }
          })
          .catch(err => {
            console.warn(err);
          })
      },
      handleStageSelection(e) {
        this.selectStage(e.target.value);
      },
      selectStage(id) {
        if (this.selectedStage === id) {
          return;
        }
        this.$nextTick(() => this.selectedStage = id);
        this.pickStage(id);
      },
      selectPreviousStage() {
        if (this.stagePosition > 0) {
          this.selectStage(this.tournaments[this.selectedTournament].stages[this.stagePosition - 1].id);
        }
      },
      selectNextStage() {
        if (this.stagePosition < this.tournaments[this.selectedTournament].stages.length - 1) {
          this.selectStage(this.tournaments[this.selectedTournament].stages[this.stagePosition + 1].id);
        }
      },

      toggleNewTournamentPopup() {
        this.creatingNewTournament = !this.creatingNewTournament;
      },
      changeNewTournamentStart(date) {
        console.log(date);
      },
      changeNewTournamentEnd(date) {
        this.newTournamentData.end_date = date;
      },
      submitNewTournament() {
        this.saveTournament(this.newTournamentData)
          .then(d => {
            this.fillNewTournamentData();
            this.toggleNewTournamentPopup();
            this.obtainTournaments()
              .then(() => {
                this.selectTournament(Math.max(...(Object.keys(this.tournaments).map(Number))));
              })
          });
      },

      toggleNewTournamentStagePopup() {
        this.creatingNewTournamentStage = !this.creatingNewTournamentStage;
      },
      changeNewTournamentStageStart(date) {
        this.newTournamentStageData.start_date = date;
        console.log(date);
      },
      changeNewTournamentStageEnd(date) {
        this.newTournamentStageData.end_date = date;
      },
      submitNewTournamentStage() {
        this.saveTournamentStage({
          ...this.newTournamentStageData,
          tournament_id: this.selectedTournament
        })
          .then(() => {
            this.toggleNewTournamentStagePopup();
            this.selectTournament(this.selectedTournament);
          });
      }
    }
  }
</script>

<style scoped>
  .header-controls {
    display: flex;
    flex-direction: row;
    justify-content: flex-start;
    align-items: flex-start;
    gap: 30px;
  }

  .pseudo-link {
    border: none;
    outline: none;
    background: none;
    border-radius: 0;
    color: #777;
    border-bottom: 1px #777 dashed;
    padding: .1em 0;
    margin-right: 10px;
    transition: .2s color;
  }

  .pseudo-link:disabled {
    color: #ddd;
    border: none;
  }

  .pseudo-link:disabled:hover {
    color: #ddd;
    border: none;
  }

  .pseudo-link:hover {
    color: #22a;
    border-bottom-color: #22a;
  }

  .modal-content {
    padding: 2rem;
  }

</style>



