<template>
    <div class="container">
      <label>
        <strong class="label">
          :
        </strong>
        <select :value="date" ref="date" @change="change" class="select">
          <option v-for="n in 31" :value="n">{{leadingZero(n)}}</option>
        </select>
      </label>
      <label>
        <strong class="label">
          :
        </strong>
        <select :value="month" ref="month" @change="change" class="select">
          <option v-for="n in 12" :value="n">{{leadingZero(n)}}</option>
        </select>
      </label>
      <label>
        <strong class="label">
          :
        </strong>
        <select :value="year" ref="year" @change="change" class="select">
          <option v-for="n in 20" :value="2017 + n">{{2017 + n}}</option>
        </select>
      </label>
    </div>
</template>

<script>
  export default {
    name: "Datepicker",
    props: ['value'],
    mounted: function () {
      this.change();
    },
    computed: {
      date: function () {
        return new Date(this.value).getDate();
      },
      month: function () {
        return (new Date(this.value).getMonth()) + 1;
      },
      year: function () {
        return new Date(this.value).getFullYear();
      }
    },
    methods: {
      leadingZero(value) {
        const num = Number(value);
        return num > 9 ? '' + num : '0' + num;
      },
      change() {
        const fullResultingDate = [
          this.leadingZero(this.$refs.year.value),
          this.leadingZero(this.$refs.month.value),
          this.leadingZero(this.$refs.date.value)
        ];

        this.$emit('change', fullResultingDate.join('-'));
      }
    }
  }
</script>

<style scoped>
  .container {
    display: flex;
  }

  select.select {
    width: auto;
  }

  strong.label {
    width: 90%;
  }

</style>



