<template>
  <div id="app">
    <tournament-table
      :get-tournaments="getTournaments"
      :get-tournament-stages="getTournamentStages"
      :pick-stage="loadStage"

      :save-tournament="submitTournamentData"
      :save-tournament-stage="submitTournamentStageData"
    />
    <grid
      :stage="stage"
      v-if="stage && Number(stage.status) !== 1"
      :set-match-score="setMatchScore"
      :finalize-match="finalizeMatch"
    />
    <new-stage
      v-if="stage && Number(stage.status) === 1"
      :seeds="Number(stage.net_size)"
      :suggest-user="suggestUser"
      :submit-users="submitTournamentRegistrationUser"
      :registered-users="stage.registrations"
      :unregister-user="unregisterPlayerFromStage"
      :close-registration="closeRegistration"
    />
  </div>
</template>

<script>
import Grid from './Grid.vue';
import NewStage from './NewStage.vue';
import TournamentTable from './TournamentTable.vue';

export default {
  name: 'app',
  components: {
    Grid,
    NewStage,
    TournamentTable
  },
  data () {
    return {
      basePath: '/administrator/index.php?option=com_tourman&',
      stage: null
    }
  },
  methods: {
    makePath(params) {
      return this.basePath + Object.keys(params)
        .map(paramName => `${encodeURIComponent(paramName)}=${encodeURIComponent(params[paramName])}`).join('&');
    },
    loadStage(stage) {
      const stageId = Number(stage);
      if (isNaN(stageId)) {
        this.stage = null;
        return;
      }

      fetch(this.makePath({task: 'get-stage', stage: stageId}))
        .then(data => data.json())
        .then(stageData => {
          this.stage = stageData;
        })
        .catch(e => console.error(e));
    },
    getTournaments() {
      return fetch(this.makePath({task: 'get-tournaments'}))
        .then(res => res.json())
        .catch(err => {
          console.warn(err);
          return {};
        });
    },
    getTournamentStages(id) {
      return fetch(this.makePath({task: 'get-tournament', tournament: id}))
        .then(res => res.json())
        .catch(err => {
          console.warn(err);
          return {};
        });
    },
    setMatchScore(id, pl1_score, pl2_score) {
      const body = new FormData();
      const raw = {
        id,
        pl1_score,
        pl2_score
      };

      Object.keys(raw).forEach(key => body.append(key, raw[key]));

      return fetch(this.makePath({task: 'set-match-score'}), {
        method: 'POST',
        body
      })
        .then(res => res.json())
        .then(data => {
          for (let i = 0; i < this.stage.games.length; i++) {
            if (Number(this.stage.games[i].id) === Number(data.id)) {
              this.stage.games[i] = {
                ...this.stage.games[i],
                ...data
              };
              this.stage = {...this.stage};

              return;
            }
          }
        })
        .catch(err => {
          console.warn(err);
        });
    },
    finalizeMatch(matchId) {
      const body = new FormData();

      body.append('matchId', matchId);

      return fetch(this.makePath({task: 'finalize-match'}), {
        method: 'POST',
        body
      })
        .then(r => r.json())
        .then(data => {
          this.loadStage(this.stage.id);
        })
        .catch(err => {
          console.warn(err);
        });
    },
    submitTournamentData(data) {
      if (!data.id) {
        const body = new FormData();

        Object.keys(data).forEach(key => body.append(key, data[key]));

        return fetch(this.makePath({
          task: 'make-new-tournament'
        }), {
          method: 'POST',
          body
        })
          .then(r => r.json())
          .then(data => {
            this.getTournaments();
            return data;
          });
      }
    },
    submitTournamentStageData(data) {
      if (!data.id) {
        const body = new FormData();

        Object.keys(data).forEach(key => body.append(key, data[key]));

        return fetch(this.makePath({
          task: 'make-new-tournament-stage'
        }), {
          method: 'POST',
          body
        })
          .then(r => r.json())
          .then(this.getTournamentStages(data.tournament_id));
      }
    },
    suggestUser(input) {
      return fetch(this.makePath({
        task: 'suggest-user',
        q: input
      }))
        .then(r => r.json());
    },
    submitTournamentRegistrationUser(users) {
      const body = new FormData();

      body.append('userIds', JSON.stringify(users.map(u => u.id)));
      body.append('stageId', this.stage.id);

      return fetch(this.makePath({
        task: 'register-players-to-stage'
      }), {
        method: 'POST',
        body
      })
        .then(r => r.json())
        .then(data => {
          this.stage.registrations = Array.isArray(data) ? data : Object.values(data);
          return data;
        })
    },
    unregisterPlayerFromStage(userId) {
      const body = new FormData();

      body.append('userId', userId);
      body.append('stageId', this.stage.id);

      return fetch(this.makePath({
        task: 'unregister-player-from-stage'
      }), {
        method: 'POST',
        body
      })
        .then(r => r.json())
        .then(data => {
          this.stage.registrations = Array.isArray(data) ? data : Object.values(data);
          return data;
        })
    },
    closeRegistration() {
      const body = new FormData();

      body.append('stageId', this.stage.id);

      return fetch(this.makePath({
        task: 'close-registration'
      }), {
        method: 'POST',
        body
      })
        .then(r => r.json())
        .then(data => {
          this.stage = data;
          return data;
        })
    }
  }
}
</script>

<style>
  h1, h2 {
    font-family: Helvetica, Arial, sans-serif;
    color: #fff;
  }
</style>



